import Axios from "axios";
import React, { useState, useEffect } from "react";
import "./scss/Main.css";

function Main() {
  const url = "https://disease.sh/v3/covid-19/all";
  const [data, setData] = useState(null);

  useEffect(() => {
    Axios.get(url).then((response) => {
      setData(response.data);
    });
  }, [url]);

  if (data) {
    return (
      <>
        <main>
          <Card id="1" name="Cases" nameToday="Today's Cases" data={data.cases} dataToday={data.todayCases} className="card" />
          <Card id="2" name="Death" nameToday="Today's Death" data={data.deaths} dataToday={data.todayDeaths} className="card" />
          <Card id="3" name="Recovered" nameToday="Today's Recovered" data={data.recovered} dataToday={data.todayRecovered} className="card" />
        </main>
      </>
    );
  }

  return (
    <>
      <main></main>
    </>
  );
}

const styles = {
    cardHeading : {
        fontWeight : 600
    }
}

function Card(props) {
  console.log(props);

  return (
    <>
      <div className="card">
        <p className="card-heading"><span style={styles.cardHeading}>{props.name}</span> : {props.data}</p>
        <p className="card-heading-today"><span style={styles.cardHeading}>{props.nameToday}</span> : {props.dataToday}</p>
      </div>
    </>
  );
}

export default Main;
